@import "../mixins";

.form {
  padding: 50px 60px;

  background: url("../img/background-form-desk.webp") no-repeat center,
    $color-background-plug;
  background-size: cover;

  @include vp-1023() {
    background: url("../img/background-form-tab.webp") no-repeat center,
      $color-background-plug;
    background-size: cover;
  }

  @include vp-374() {
    background: url("../img/background-form-mob.webp") no-repeat center,
      $color-background-plug;
    background-size: cover;
  }
}
.form__container {
  display: grid;
  grid-template-columns: 50% 40%;
  grid-template-areas:
    "a c"
    "b c";
  column-gap: 10%;
  row-gap: 30px;

  @include vp-1023() {
    display: flex;
    flex-direction: column;
  }
}
.form__header {
  grid-area: a;

  margin: 0;
  padding: 0;
  align-self: flex-end;

  font-family: "Montseratt", Arial, sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
  color: $color-default-black;

  @include vp-1023() {
    align-self: center;
  }
}

.form__description {
  grid-area: b;

  margin: 0;
  padding: 0;

  font-family: "Montseratt", Arial, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: $color-default-black;

  @include vp-1023() {
    align-self: center;
    max-width: 352px;
  }
}

.form div {
  grid-area: c;
}

.form__form {
  display: flex;
  flex-direction: column;
  row-gap:  20px;

  padding: 50px;
  background-color: $color-default-black;
}

.form__form label {
  display: flex;

  font-family: "Montseratt", Arial, sans-serif;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: $color-default-white;
}

.form__form input {
  width: 100%;
  min-height: 43px;
  padding: 10px 15px;
  border: none;
}

.form__form h3 {
  max-width: 303px;

  font-family: "Montseratt", Arial, sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: $color-default-white;
}

